vante para una variedad de públicos.")])])]),_c('div',{staticClass:"row justify-content-center",attrs:{"titulo":"02"}},[_c('div',{staticClass:"col-lg-2 col-4 mb-4 mb-lg-0"},[_c('figure',[_c('img',{staticClass:"img200",attrs:{"src":require("@/assets/curso/temas/tema3/icono-02.svg"),"alt":"Imagen decorativa"}})])]),_c('div',{staticClass:"col-lg-10 mb-4 mb-lg-0"},[_c('h4',[_vm._v("Identificar los atributos únicos del destino o servicio")]),_c('p',[_vm._v("Colombia es un país con una rica diversidad natural y cultural. Los recursos naturales, como selvas, montañas, playas, y su diversidad cultural (pueblos indígenas, gastronomía, patrimonio histórico), son algunos de los elementos clave que deben destacarse en la propuesta de valor. Definir qué hace único al destino o servicio es esencial. ¿Es una experiencia en el Amazonas?, ¿Un recorrido gastronómico por Cartagena?, y ¿O un tour de avistamiento de aves en el Eje Cafetero? Identificar estos elementos diferenciadores es fundamental.")])])]),_c('div',{staticClass:"row justify-content-center",attrs:{"titulo":"03"}},[_c('div',{staticClass:"col-lg-2 col-4 mb-4 mb-lg-0"},[_c('figure',[_c('img',{staticClass:"img200",attrs:{"src":require("@/assets/curso/temas/tema3/icono-03.svg"),"alt":"Imagen decorativa"}})])]),_c('div',{staticClass:"col-lg-10 mb-4 mb-lg-0"},[_c('h4',[_vm._v("Desarrollar una oferta centrada en el cliente")]),_c('p',[_vm._v("La propuesta de valor debe centrarse en resolver las necesidades y deseos de los turistas. Para esto, es importante pensar en cómo se puede hacer que su experiencia sea más memorable, cómoda o auténtica. Además, es importante ofrecer experiencias personalizadas que se adapten a las necesidades de diferentes grupos de turistas: desde solitarios hasta grandes grupos o familias. Esta personalización ayuda a mejorar la satisfacción del cliente.")])])]),_c('div',{staticClass:"row justify-content-center",attrs:{"titulo":"04"}},[_c('div',{staticClass:"col-lg-2 col-4 mb-4 mb-lg-0"},[_c('figure',[_